    // background-color: hsl(0, 0%, 100%);
    border-radius: 50%;
    width: 4px;
    height: 4px;
    position: absolute;
    right: 1px;
    top: 0;
    z-index: 1;
}
.progress_bar .progress-bar-inner:after {
    content: "";
    width: 14px;
    height: 14px;
    border: 1px solid #F0F2F9;
    background: #FFFFFF;
    border-radius: 50%;
    position: absolute;
    right: -4px;
    top: -5px;
}
}

